%tabs {
    position: relative;
    padding: 0;
    background: rgba(gray, .2);
    width: 100%;
    border-radius: 5px;

    &:hover {
        background: rgba(gray, .3);
    }

    .tab {
        height: 30px;
        display: flex;
        align-items: center;
        margin: 0;
        border: none;
        float: none !important;
        border-radius: 0;
        box-shadow: none;
        background: $c-purple;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;

        span {
            font-family: $font-opensans;
            font-weight: 700;
            text-transform: none;
            color: $c-white;
            @include antialias;
        }

        a {
            &:before {
                color: white;
            }
        }
    }

    .settings {
        margin-left: auto;
    }

    .field-options-preview {
        display: flex;
        background: $c-moss;
        padding: 0 12px 10px;
        margin-top: -4px;

        &:before {
            display: none !important;
        }

        & > div {
            color: rgba(white, .5);
            margin-right: 10px;
            font-weight: 100;
            letter-spacing: .05em;
        }

        .preview-type {
            letter-spacing: normal;
        }
    }
}

%fld-tabcontent {
    position: relative;
    padding: 0;
    border: none;
}

%fld-field {
    padding: 15px 20px;
    border: none;
    border-bottom: 1px solid rgba(gray, .1);
    border-left: 1px solid rgba(gray, .1);
    border-right: 1px solid rgba(gray, .1);
    background: $c-white;

    &:hover {
        background: darken(white, 1);
    }

    span:not(.preview-type) {
        font-weight: 500;
        font-size: 12px;
        text-transform: none;
        color: $c-textDarkBlue;
    }

    & + .fld-field { margin: 0; }

    .field-icon {
        display: inline-block;
        width: 20px;
        text-align: center
    }

    &.field-not-allowed {

        &:after {
            display: block;
            content: "Field not allowed, will not display";
            font-size: 10px;
            color: rgba($c-red, .6);
        }
    }
}

%fld-insertion {
    margin: 25px !important;
    border: 2px dashed $c-purple;
    background-color: rgba($c-purple, .1);

    .tabs, .tab, .fld-tabcontent {
        background: none;
        border: none;
    }
}

.qarr {
    .fields-wrapper {
        width: 100%;
        padding-top: 30px;

        h1 {
            font-family: $font-semiBold;
            font-size: 16px;
            margin-bottom: 80px;
        }
    }

    #fieldlayoutform {
        position: relative;
        background-image: none;
        background: white;
        border-radius: 5px;
        border: 1px solid $c-border;
        padding: 25px;

        .field-warning {
            font-family: $c-textDark;
            font-size: 12px;
            color: $c-lightGray;

            a {
                color: $c-purple;

                &:hover {
                    color: $c-darkPurple;
                }
            }
        }

        hr {
            border-color: darken($c-border, 5);
        }

        .newtabbtn-container {
            position: absolute;
            top: -50px;
            left: 0;
            z-index: 1;
            padding: 0;
            margin: 0;

            &:before { display: none; }

            .btn.icon {
                padding: 12px 35px 10px;
                &:before {
                    content: "+";
                }
            }

            .settings-button {
                display: flex;
                margin-left: auto;
            }
        }

        // Global settings preview
        .settings-preview {
            display: flex;
            flex-direction: column;
            justify-content: center;
            font-size: 10px;
            color: rgba($c-gray, .6);
            line-height: 1.2;
            margin-left: 10px;

            span {
                text-transform: uppercase;
                font-weight: 900;
                font-size: 8px;
            }
        }

        header {
            padding: 0;
            margin-bottom: 20px;
            border-bottom: none;

            &.unused-header {
                margin-bottom: 0;
                padding: 25px;
                border: 1px solid $c-border;
                border-bottom: none;
                background-color: white;
                border-top-left-radius: $border-radius;
                border-top-right-radius: $border-radius;
            }

            p {
                padding: 0;
                margin: 0;
                font-size: 10px;
                font-weight: 100;
                color: $c-textLight;
                text-transform: none;
            }
        }
    }

    .fld-tabs {
        margin: 0 !important;
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;

        & > .fld-tab {
            float: none !important;
            margin: 0 3.5% 3.5% 0 !important;
            padding: 0 !important;
            width: 31%;

            &:nth-child(3n + 4) {
                margin-right: 0 !important;
            }
        }

        & > .fld-tab.fld-tab-caboose {
            border: 2px dashed $c-border;
            padding: 0 !important;
            margin: 0 !important;
        }
    }

    .unusedfields {
        margin: 0 !important;
        padding: 0 !important;
        box-sizing: border-box;
        background-color: white;
        //border-top: 1px dashed $c-border;
        border-bottom-left-radius: $border-radius;
        border-bottom-right-radius: $border-radius;

        .heading {
            padding: 20px 25px;
            margin: 20px -25px 20px -25px;
            display: flex;
            align-items: center;
            background: $c-border;

            p {
                font-family: $font-bold;
                font-size: 10px;
                color: darken($c-border, 50);
                letter-spacing: .05em;
                text-transform: uppercase;
                @include antialias;
            }
        }

        .fld-tab {
            width: 31%;
            margin-right: 2.3333%;
        }
    }


    // Field Tabs
    .fld-tab {
        margin-left: 0 !important;
        // margin-right: 0 !important;
        margin-bottom: 30px !important;
        height: 100% !important;
        box-sizing: border-box;

        .tabs {
            @extend %tabs;
        }

        .fld-tabcontent {
            @extend %fld-tabcontent;

            .fld-field {
                @extend %fld-field;

                &.fld-required {
                    span:after {
                        color: $c-red;
                    }
                }

                a {
                    &:before {
                        color: #c9c9c9;
                    }
                }

                & + .fld-field { margin: 0; }

                // Background insertion container
                &.fld-insertion {
                    @extend %fld-insertion;
                    margin: 0 !important;
                }
            }

            .fld-tab.fld-tab-caboose {
                //position: absolute;
                margin: 0 !important;
                padding: 0 !important;
                min-height: auto !important;
                height: 52px !important;
                width: 100%;
                border: 2px dashed $c-border !important;
                box-sizing: border-box;
            }
        }

        // Background insertion container
        &.fld-insertion {
            @extend %fld-insertion;
        }
    }

    // Dragging
    .draghelper {

        // Tab being dragged
        &.fld-tab {

            .tabs { @extend %tabs; }

            .fld-tabcontent {
                @extend %fld-tabcontent;

                .fld-field { @extend %fld-field; }
            }
        }

        // Field being dragged
        &.fld-field {
            @extend %fld-field;

            .settings:before { color: $c-textLight !important; opacity: .2; }
        }
    }
}


