@mixin icon {
    font-family: 'Craft';
    speak: none;
    -webkit-font-feature-settings: "liga", "dlig";
    -moz-font-feature-settings: "liga=1, dlig=1";
    -moz-font-feature-settings: "liga", "dlig";
    -ms-font-feature-settings: "liga", "dlig";
    -o-font-feature-settings: "liga", "dlig";
    font-feature-settings: "liga", "dlig";
    text-rendering: optimizeLegibility;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    direction: ltr; // Fixes a rendering issue in Chrome/Win
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;

    display: inline-block;
    text-align: center;
    font-style: normal;
    vertical-align: middle;
    word-wrap: normal !important;
    user-select: none;
}

.qarr {
    .toolbar {
        background: white;
        border-radius: 4px;
        padding: 7px 1px 0;
        //z-index: 2;
        //border-left: 1px solid transparent;

        //&.fixed {
        //    border-top: 1px solid $c-border;
        //    border-left: 1px solid $c-border;
        //    border-color: $c-border;
        //
        //    padding-bottom: 10px;
        //    padding-top: 7px;
        //    margin-left: -1px !important;
        //}
        //
        //.flex > * {
        //    margin: 0 !important;
        //}

        .btn {
            height: 100%;
            padding: 10px 15px;
            box-shadow: none !important;
            background: $c-border;
            font-family: $font-opensans;
            font-weight: 600;
            color: darken($c-border, 50);
            border-radius: 5px;
            @include antialias;

            &.menubtn:empty {
                padding-left: 20px;
                padding-right: 20px;
            }

            &:after {
                transition: transform .3s ease;
            }

            &.active {
                &:after {
                    transform: rotate(180deg);
                }
            }
        }

        .elements-selected-count {
            font-family: $font-semiBold;
            font-size: 12px;
        }
    }

    .selectallcontainer {
        .btn {
            background: none !important;
            box-shadow: none;
        }

        .checkbox {
            &:before {
                @include icon;
                content: 'check';
                box-shadow: none;
                border: none;
                font-size: 20px !important;
                color: $c-border;
                background: none;
            }

            &.checked {
                &:before {
                    color: $c-purple;
                }
            }
        }
    }

    .elements .tableview td.checkbox-cell div.checkbox {
        top: calc(50% - 11px);
    }

    .elements {
        .checkbox {

            &:before {
                @include icon;
                content: 'check';
                box-shadow: none;
                border: none;
                font-size: 20px !important;
                color: $c-border;
                background: none;
            }
        }

        .sel {
            .checkbox {
                &:before {
                    color: $c-purple;
                }
            }
        }
    }

    .element {
        padding: 0;

        a {
            color: $c-gray;
            font-weight: 700 !important;
            transition: all .3s ease;

            &:hover {
                color: $c-purple;
            }
        }
    }

    .badge-wrapper {
        .entry-badge {
            display: flex;
            justify-content: center;
            align-items: center;
            padding: 0;
            border-radius: 4px;
            width: 28px;
            height: 28px;
            text-transform: uppercase;

            font-family: $font-bold;
            font-size: 10px;
        }
    }

    // Guest Wrapper
    .guest-wrapper {
        display: flex;

        .guest-avatar {
            position: relative;

            > * {
                border-radius: 5px;
            }
        }

        img {
            display: block;
            width: 35px;
            border-radius: 5px;
            opacity: .5;
            transition: all .3s ease;
        }

        &:hover {
            .purple-blanket {
                opacity: 0;
            }

            img {
                opacity: 1;
            }
        }

        .guest-meta {
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;

            span {
                display: block;
                transition: all .3s ease;
                line-height: normal;
                @include antialias;
            }

            .guest-name {
                font-family: $font-bold;
            }

            .guest-email {
                color: $c-lightGray;
                font-size: 10px;
            }
        }
    }

    // Reporting
    .flags-wrapper {
        display: inline-block;

        svg {
            font-size: 16px;
            margin-right: 7px;
        }

        .flagged-item {
            display: flex;
            align-items: center;
            text-transform: uppercase;
            font-family: $font-bold;
            font-size: 10px;
            letter-spacing: .05em;
            line-height: 1;
            padding: 4px 8px;
            border: 1px solid $c-border;
            border-radius: 5px;
            margin-right: 5px;
            margin-bottom: 5px;
        }

    }

    // Ratings
    .rating-wrapper {
        span {
            padding-right: 2px;
        }

        svg {
            color: $c-border;
        }

        .active {
            svg {
                color: $c-yellow;
            }
        }
    }

    // Product Wrapper
    .product-wrapper {
        display: flex;

        .product-meta {
            display: flex;
            flex-direction: column;

            span {
                display: block;
                line-height: 1;
            }
        }

        .product-badge-wrapper {
            .product-badge {
                display: flex;
                justify-content: center;
                align-items: center;
                padding: 0;
                border-radius: 4px;
                width: 28px;
                height: 28px;
                font-weight: 600;
            }
        }

        .product-name {
            font-weight: 600;
            padding-bottom: 4px;
        }

        .product-type {
            color: $c-lightGray;
            font-size: 10px;
        }
    }

    // Date Wrapper {
    .date-wrapper {
        color: $c-lightGray;
    }

    // Overview Wrapper
    .overview-wrapper {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;

        & > div {
            flex: 1;
            border-radius: 5px;
            padding: 20px;
            margin: 0 10px 30px 10px;
            text-align: center;

            &.items-total {
                background: $c-purple;
            }

            &.items-pending {
                background: $c-blue;
            }

            &.items-approved {
                background: $c-green;
            }

            &.items-rejected {
                background: $c-red;
            }
        }

        .overview-heading {
            font-family: $font-roboto;
            font-size: 16px;
            font-weight: 700;
            letter-spacing: .02em;
            color: white;
            margin-bottom: 10px;
            @include antialias();
        }

        .overview-value {
            font-size: 21px;
            font-weight: 800;
            color: white;
        }
    ;
    }

    // Feedback Wrapper
    .feedback-wrapper {
        color: $c-gray;
    }

    // Element Table
    table {
        border-collapse: separate;
        border-spacing: 0 5px;

        thead {
            th {
                font-family: $font-bold;
                font-size: 10px;
                color: $c-lightGray;
                letter-spacing: .1em;
                text-transform: uppercase;
                padding: 15px 0 15px 15px !important;
                @include antialias();
            }
        }

        tbody {
            td {
                background: white;
                padding: 15px !important;
                font-size: 12px;
                font-weight: 400;
                letter-spacing: .02em;
                transition: all .3s ease;
                border: none !important;
                @include antialias();
            }

            tr {
                td.checkbox-cell {
                    padding-right: 25px !important;
                }
                td {
                    &:first-child {
                        border-top-left-radius: 3px;
                        border-bottom-left-radius: 3px;
                    }
                    &:last-child {
                        border-top-right-radius: 3px;
                        border-bottom-right-radius: 3px;
                    }
                }

                &.sel {
                    td:not(.checkbox-cell) {
                        background: rgba(95, 106, 130, 0.1) !important;
                        box-shadow: none !important;
                    }
                }
            }
        }
    }
}


